body {
  font-family: "Spartan", sans-serif;
  color: #121212;
}
.sort__main {
  max-width: 1110px;
  margin: 0 auto;
}
.sort__title {
  height: 169px;
  padding-top: 96px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 1220px) {
  .sort__navigation {
    flex-wrap: wrap;
    justify-content: center;
    gap: 25px;
    padding: 25px 0 0 0;
  }
}
@media (max-width: 400px) {
  .sort__navigation {
    display: none;
  }
}
.sort__logo {
  text-transform: uppercase;
  font-size: 22px;
  font-weight: 600;
  line-height: 25px;
  letter-spacing: 0.06em;
}
.sort__navigation {
  display: flex;
  align-items: center;
  justify-content: center;
}
.sort__navigation li {
  font-size: 12px;
  font-weight: 400;
  line-height: 13px;
  letter-spacing: 0.06em;
  opacity: 0.6;
  padding-left: 32px;
}
.sort__navigation li:first-child {
  font-weight: 500;
  opacity: 1;
  padding-left: 0;
}
.sort__items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  flex-wrap: wrap;
}
.item__card {
  max-width: 255px;
}
.item__card-img {
  max-width: 255px;
  max-height: 318px;
}
.item__card-title {
  padding-top: 16px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  opacity: 0.6;
  color: #121212;
}
.item__card-suptitle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.item__card-price {
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  padding-top: 12px;
  color: #121212;
}
.item__card-score {
  background-image: url(../../../assets/img/men-img/4stars.svg);
  width: 70px;
  height: 14px;
}
.sort__button {
  margin: 48px auto 0 auto;
  font-size: 13px;
  font-weight: 500;
  line-height: 48px;
  letter-spacing: 0.06em;
  text-align: center;
  max-width: 1110px;
  height: 48px;
  border: 2px solid #f8f8f8;
  background-color: #f8f8f8;
  text-transform: uppercase;
}